































































































































































































































































































































































































































































































































































































































































































































































import {Vue, Component} from "vue-property-decorator"
import {Page, DatePicker, RadioGroup, Radio, Tree} from 'view-design'
import tabNav from '@manage/components/tabNav.vue';
import mixSelect from '@manage/components/mixSelect.vue';
import Input from '@manage/components/normalInput.vue';
import * as api from '@manage/api/serve/order'
import * as app from '@manage/api/app'

@Component({
    filters: {
        rounding(value) {
            return value.toFixed(2)
        }
    },
    components: {
        Page,
        DatePicker,
        tabNav,
        mixSelect,
        Input,
        Radio,
        RadioGroup,
        Tree
    }
})

export default class repairRecord extends Vue {
    areaList: Array<any> = [];
    orderStateList: any = [];
    imgList: any = [`s`];
    recordList: Array<any> = [];
    restaurants: any = [
        {"name": "王1", "value": "长宁区新渔路144号"},
        {"value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号"},
    ]
    state1: string = ''

    formHeight: number = 600;
    sendFlag: boolean = false
    rdId: string = ''
    pages: any = {
        pageNum: 1,
        pageSize: 10,
        totalElements: 20,
        placeType: '',
        orderState: '',
        key: '',
        startTime: '',
        endTime: '',
    };
    timeArr: any = [];
    buildingTree: any = [
        {
            title: 'parent 1',
            level: 1,
            id: '1'
        },
        {
            title: 'parent 1',
            level: 1,
            id: '2'
        },
        {
            title: 'parent 1',
            level: 1,
            id: '3'
        }
    ]
    loading: boolean = false;
    subFlag: boolean = true;
    addDialogVisible: boolean = false
    loaderOption: any = {
        lock: true,
        target: '#viewbox',
        text: '加载中',
        spinner: 'el-icon-loading',
    };

    addObject: any = {
        placeTypeCode: '',
        address: '',
        orderDetail: '',
        initiatorName: '',
        initiatorPhone: '',
        orderStartTime: '',
        orderEndTime: '',
        acceptanceFee: '20',
        "fileStorages": [],
    }


    orderDetail: any = {
        initiatorName: '',
        initiatorPhone: '',
        address: '',
        placeTypeCode: '',
        placeTypeName: '',
        orderStartTime: '',
        orderDetail: '',
    }
    serverStar: number = 0;
    serverRemark: string = '';
    setDialogVisible: boolean = false
    dispatchDialogVisible: boolean = false

    roomContent(h, {root, node, data}) {
        return h('div', {
                style: {
                    display: 'inline-flex',
                    alignItems: 'center',
                    height: '36px',
                    overflow: 'hidden',
                    width: 'calc(100% - 20px)'
                },
                class: {
                    treeActive: this.rdId === data.id,
                },
                on: {
                    click: (event) => {
                        if (data.level === 1) {
                            data.checked = !data.checked;
                            this.rdId = data.id
                        } else {
                            data.expand = !data.expand;
                        }
                    }
                }
            },
            [
                h('i', {
                    class: {
                        iconfont: true,
                        iconxiaoqu: data.level === 1,
                    },
                    style: {
                        lineHeight: `1`,
                        marginRight: '8px',
                        marginLeft: '8px',
                        cursor: `pointer`,
                    },
                }),

                h('i', {
                    attrs: {
                        title: data.title,
                    },
                    style: {
                        fontStyle: 'normal',
                        fontSize: '14px',
                        lineHeight: `16px`,
                        cursor: `pointer`,
                        overflow: `hidden`,
                        whiteSpace: `nowrap`,
                        textOverflow: `ellipsis`
                    },
                }, data.title),


            ]);
    };

    async getSelection(name: string, code: string) {
        let data = await api.getSelection(code);
        this[`${name}`] = [];
        for (let k in data) {
            this[`${name}`].push({
                id: k,
                name: data[k]
            })
        }
    }

    mounted() {
        this.getSelection(`areaList`, `placeType`)
        this.getSelection(`orderStateList`, `orderState`)
        this.$nextTick(() => {
            this.setPagesize();
            this.getOrderList();
        })
    }

    setPagesize() {
        let refs: any = this.$refs;
        let maxHeight = refs.viewbox.clientHeight - 15;
        let searchline = refs.searchline.clientHeight;

        //Page组件高度为53px
        this.formHeight = maxHeight - searchline - 53;
        this.pages.pageSize = Math.floor((this.formHeight - 49) / 48);
    }

    async getOrderList() {
        this.loading = true;
        try {
            let {totalElements, content} = await api.getOrderList({
                pageNum: this.pages.pageNum,
                pageSize: this.pages.pageSize,
                placeType: this.pages.placeType,
                orderState: this.pages.orderState,
                key: this.pages.key,
                startTime: this.pages.startTime,
                endTime: this.pages.endTime,
            });
            this.pages.totalElements = totalElements
            this.recordList = content
        } catch (e) {

        }
        this.loading = false
    }

    reset() {
        this.pages.placeType = '';
        this.pages.orderState = '';
        this.pages.key = '';
        this.pages.startTime = ''
        this.pages.endTime = ''
        this.timeArr = []
        this.getPage();
    }

    getPage(page?: number) {
        this.pages.pageNum = page || 1;
        this.getOrderList();
    }

    setTimelist(arr: any) {
        //+ ` 00:00:00`
        this.pages.startTime = arr[0]
        this.pages.endTime = arr[1]
    }


    imgportadd(src) {
        return src ? this.$imgDownUrl() + `${src}` : null
    }

    upAddImg(e) {
        let fd = new FormData();
        let type = `bmp,jpg,jpeg,png`;
        let bool2 = type.indexOf(e.target.files[0].type.replace(`image/`, ``)) > -1;
        if (!bool2) {
            this.$message({
                showClose: true,
                message: `图片格式错误！支持格式：bmp,jpg,jpeg,png。`,
                type: 'error'
            });
        } else {
            fd.append("file", e.target.files[0]);
            fd.append("relativePath", '');
            let load:any = this.$loading(this.loaderOption);
            setTimeout(async () => {
                try {
                    let {data: {id}} = await app.upImg(fd, this.$upBaseUrl());
                    this.addObject.fileStorages.push(id);
                    this.$message.success(`上传成功！`)

                } catch (e) {
                    this.$message.error(`上传失败!`)
                }
                load.close()
                (<HTMLInputElement>document.getElementById('addupload')).value = '';
            }, 200);
        }
    }

    deleteUplist(index) {
        this.addObject.fileStorages.splice(index, 1);
    }

    addOrder() {
        if (this.subFlag) {
            this.subFlag = false;
            let load:any = this.$loading(this.loaderOption);

            api.addOrder(this.addObject).then(() => {
                this.$message({
                    showClose: true,
                    message: `新增成功！`,
                    type: 'success'
                });
                this.reset();
                this.closeAddDidalog();
            }).catch(() => {
                this.$message({
                    showClose: true,
                    message: `操作失败！请检查网络连接。`,
                    type: 'error'
                });
            })
            load.close();
            this.subFlag = true;
        }
    }

    setOrderStartTime(val) {
        this.addObject.orderStartTime = val;
    }

    setOrderEndTime(val) {
        this.addObject.orderEndTime = val;
    }

    get sDateOpt() {
        let htu: any = {}
        if (this.addObject.orderEndTime) {
            let timer = new Date(this.addObject.orderEndTime).getTime() - 86400000;
            htu = {
                disabledDate(time) {
                    return time.getTime() > timer
                }
            };
        }
        return htu;
    }

    get eDateOpt() {
        let htu: any = {}
        if (this.addObject.orderStartTime) {
            let timer = new Date(this.addObject.orderStartTime).getTime();
            htu = {
                disabledDate(time) {
                    return time.getTime() < timer
                }
            };
        }
        return htu;
    }

    closeAddDidalog() {
        this.addObject = {
            placeTypeCode: '',
            address: '',
            orderDetail: '',
            initiatorName: '',
            initiatorPhone: '',
            orderStartTime: '',
            orderEndTime: '',
            acceptanceFee: '20',
            "fileStorages": [],
        }
        let orderStartTime: any = this.$refs.orderStartTime;
        orderStartTime.handleClear();

        this.addDialogVisible = false
    }

    deleteOrder(id: string) {
        this.$confirm('您确认要删除这条记录吗?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            api.deleteOrder([id]).then(() => {
                this.$message({
                    showClose: true,
                    message: `删除成功！`,
                    type: 'success'
                });
                this.reset();
            }).catch(() => {
                this.$message({
                    showClose: true,
                    message: `删除失败！`,
                    type: 'error'
                });
            })
        }).catch(() => {

        });
    }

    closeSetDidalog() {
        this.setDialogVisible = false
    }

    state: string = ''


    async openDispatchDialog(row) {
        try {
            let {iparkRmOrderInfo} = await api.getOrderDetail(row.id);
            this.orderDetail = iparkRmOrderInfo;
            this.orderDetail.placeTypeName = row.placeTypeName;
            this.dispatchDialogVisible = true
        } catch (e) {
            this.$message.error(`获取工单详情失败！`)
        }
    }

    closeDispatchDialog() {

        this.dispatchDialogVisible = false;
        this.orderDetail = {
            initiatorName: '',
            initiatorPhone: '',
            address: '',
            placeTypeCode: '',
            placeTypeName: '',
            orderStartTime: '',
            orderDetail: '',
        }

    }


    addOrderServe() {
        if (this.subFlag) {
            this.subFlag = false;
            let load:any = this.$loading(this.loaderOption);

            api.addOrderServer({
                serverStar: this.serverStar,
                remark: this.serverRemark,
                orderId: this.orderDetail.id,
            }).then(() => {
                this.$message({
                    showClose: true,
                    message: `评价成功！`,
                    type: 'success'
                });
                this.reset();
                this.closeDispatchDialog();
                load.close();
                this.subFlag = true;
            }).catch(() => {
                this.$message({
                    showClose: true,
                    message: `操作失败！请检查网络连接。`,
                    type: 'error'
                });
                load.close();
                this.subFlag = true;
            })
        }
    }


    querySearch(queryString, cb) {
        let restaurants = this.restaurants;
        console.log(restaurants);
        let results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // 调用 callback 返回建议列表的数据
        cb(results);
    }

    createFilter(queryString) {
        return (restaurant) => {
            return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    }

    handleSelect(item) {
        console.log(item);
        console.log(this.state1);
    }
}
